import {Injectable} from "@angular/core";
import {AngularFire, AuthProviders, AuthMethods, FirebaseListObservable, FirebaseObjectObservable,FirebaseAuthState} from 'angularfire2';
import { Observable, BehaviorSubject } from "rxjs/Rx";
import * as firebase from 'firebase';



@Injectable()
export class AF {

  public users: FirebaseListObservable<any>;
  public displayName: string;
  public email: string;
  public user: FirebaseObjectObservable<any>;
  private _authState: FirebaseAuthState = null;
  private _isAuthenticated = new BehaviorSubject<boolean>(false); // false is the initial value


  constructor(private af: AngularFire) {


    // Init user state
    af.auth.subscribe((state: FirebaseAuthState) => {
      this._authState = state;
      this._isAuthenticated.next(this._authState !== null);
    });
  }





  get authObservable(): Observable<boolean> {
    return this._isAuthenticated.asObservable();
  }
  // used by the router (not an observable, only need to be checked once)
  get isAuthenticated(): boolean {
    return this._authState !== null;
  }





  /**
   * Logs in the user
   * @returns {firebase.Promise<FirebaseAuthState>}
   */
  loginWithGoogle() {
    return this.af.auth.login({
      provider: AuthProviders.Google,
      method: AuthMethods.Popup,
    });
  }

  loginWithFacebook() {
    return this.af.auth.login({
      provider: AuthProviders.Facebook,
      method: AuthMethods.Popup,
    });
  }

  /**
   * Logs out the current user
   */
  logout() {
    return this.af.auth.logout();
  }


  /**
   *
   */
  addUserInfo(){
    //We saved their auth info now save the rest to the db.
    this.users.push({
      email: this.email,
      displayName: this.displayName
    });
  }


  /**
   *
   * @param model
   * @returns {firebase.Promise<void>}
   */
  registerUser(email, password) {
    console.log(email)
    return this.af.auth.createUser({
      email: email,
      password: password
    });


  }

  /**
   *
   * @param uid
   * @param model
   * @returns {firebase.Promise<void>}
   */
  saveUserInfoFromForm(uid, name, email) {
    return this.af.database.object('registeredUsers/' + uid).set({
      name: name,
      email: email,
    });
  }

  /**
   * Logs the user in using their Email/Password combo
   * @param email
   * @param password
   * @returns {firebase.Promise<FirebaseAuthState>}
   */
  loginWithEmail(email, password) {
    return this.af.auth.login({
        email: email,
        password: password,
      },
      {
        provider: AuthProviders.Password,
        method: AuthMethods.Password,
      });
  }

}



